"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

// Mock data for demonstration
const MOCK_ORDERS = [
  {
    id: "ORD001",
    customerName: "John Doe",
    design: "Classic Suit",
    price: 599.99,
    status: "In Progress",
    orderDate: "2024-02-15",
    estimatedDelivery: "2024-03-01",
    progress: 60,
  },
  {
    id: "ORD002",
    customerName: "Jane Smith",
    design: "Traditional Kaftan",
    price: 299.99,
    status: "Ready for Fitting",
    orderDate: "2024-02-10",
    estimatedDelivery: "2024-02-25",
    progress: 80,
  },
  {
    id: "ORD003",
    customerName: "Mike Johnson",
    design: "Business Shirt",
    price: 89.99,
    status: "New Order",
    orderDate: "2024-02-18",
    estimatedDelivery: "2024-03-05",
    progress: 0,
  },
]

const ORDER_STATUSES = [
  "New Order",
  "In Progress",
  "Ready for Fitting",
  "Completed",
  "Cancelled",
]

interface OrderDetailsProps {
  order: typeof MOCK_ORDERS[0]
  onUpdateStatus: (orderId: string, newStatus: string) => void
}

function OrderDetails({ order, onUpdateStatus }: OrderDetailsProps) {
  return (
    <div className="space-y-6">
      <div className="grid grid-cols-2 gap-4">
        <div>
          <p className="text-sm text-gray-500">Order ID</p>
          <p className="font-medium">{order.id}</p>
        </div>
        <div>
          <p className="text-sm text-gray-500">Customer</p>
          <p className="font-medium">{order.customerName}</p>
        </div>
        <div>
          <p className="text-sm text-gray-500">Order Date</p>
          <p className="font-medium">{order.orderDate}</p>
        </div>
        <div>
          <p className="text-sm text-gray-500">Estimated Delivery</p>
          <p className="font-medium">{order.estimatedDelivery}</p>
        </div>
      </div>

      <div>
        <p className="text-sm text-gray-500 mb-2">Status</p>
        <Select
          value={order.status}
          onValueChange={(value) => onUpdateStatus(order.id, value)}
        >
          <SelectTrigger>
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {ORDER_STATUSES.map((status) => (
              <SelectItem key={status} value={status}>
                {status}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      <div>
        <h3 className="font-semibold mb-2">Progress</h3>
        <div className="w-full bg-gray-200 rounded-full h-2.5">
          <div
            className="bg-black h-2.5 rounded-full"
            style={{ width: `${order.progress}%` }}
          ></div>
        </div>
        <p className="text-sm text-gray-500 mt-1">
          {order.progress}% Complete
        </p>
      </div>

      <div className="pt-4 border-t">
        <h3 className="font-semibold mb-2">Total Price</h3>
        <p className="text-2xl font-bold">${order.price.toFixed(2)}</p>
      </div>
    </div>
  )
}

export default function AdminDashboard() {
  const [selectedOrder, setSelectedOrder] = useState<typeof MOCK_ORDERS[0] | null>(null)

  const handleUpdateStatus = (orderId: string, newStatus: string) => {
    // In production, implement API call to update order status
    console.log(`Updating order ${orderId} status to ${newStatus}`)
  }

  // Calculate statistics
  const totalOrders = MOCK_ORDERS.length
  const inProgress = MOCK_ORDERS.filter(o => o.status === "In Progress").length
  const readyForFitting = MOCK_ORDERS.filter(o => o.status === "Ready for Fitting").length
  const newOrders = MOCK_ORDERS.filter(o => o.status === "New Order").length

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-bold font-poppins mb-2">
          Admin Dashboard
        </h1>
        <p className="text-gray-600">
          Manage orders and track progress
        </p>
      </div>

      {/* Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500">Total Orders</h3>
          <p className="text-3xl font-bold mt-2">{totalOrders}</p>
        </Card>
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500">In Progress</h3>
          <p className="text-3xl font-bold mt-2">{inProgress}</p>
        </Card>
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500">Ready for Fitting</h3>
          <p className="text-3xl font-bold mt-2">{readyForFitting}</p>
        </Card>
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500">New Orders</h3>
          <p className="text-3xl font-bold mt-2">{newOrders}</p>
        </Card>
      </div>

      {/* Recent Orders */}
      <div>
        <h2 className="text-xl font-semibold mb-4">Recent Orders</h2>
        <div className="space-y-4">
          {MOCK_ORDERS.map((order) => (
            <Card key={order.id} className="p-6">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="font-semibold">{order.customerName}</h3>
                  <p className="text-sm text-gray-500">
                    {order.design} - Order ID: {order.id}
                  </p>
                </div>
                <div className="text-right">
                  <p className="font-medium">${order.price.toFixed(2)}</p>
                  <p className="text-sm text-gray-500">{order.status}</p>
                </div>
              </div>

              <div className="mt-4">
                <div className="w-full bg-gray-200 rounded-full h-2.5">
                  <div
                    className="bg-black h-2.5 rounded-full"
                    style={{ width: `${order.progress}%` }}
                  ></div>
                </div>
              </div>

              <div className="mt-4 flex justify-end space-x-4">
                <Button
                  variant="outline"
                  onClick={() => setSelectedOrder(order)}
                >
                  View Details
                </Button>
                <Button
                  className="bg-black text-white hover:bg-gray-900"
                  onClick={() => {
                    // In production, implement chat functionality
                    alert("Chat feature coming soon!")
                  }}
                >
                  Contact Customer
                </Button>
              </div>
            </Card>
          ))}
        </div>
      </div>

      <Dialog open={!!selectedOrder} onOpenChange={() => setSelectedOrder(null)}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Order Details</DialogTitle>
          </DialogHeader>
          {selectedOrder && (
            <OrderDetails
              order={selectedOrder}
              onUpdateStatus={handleUpdateStatus}
            />
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
